<template>
	<div class="container">
        <div class="PlayerRanking_header">
            <img @click="toReturn" :src="staticImgH+'zuojiantou.png'" alt="">
            <span></span>
            <span ></span>
        </div>
        <div class="play">
            <video-player class="video-player-box ovideo" controls="controls" autoplay="autoplay" ref="videoPlayer" :options="playerOptions" :playsinline="true">
            </video-player></div>
        </div>
</template>
 
<script>
import {mapState} from 'vuex'
  import {mapMutations} from 'vuex'
  import {videoPlayer} from 'vue-video-player'
  import 'video.js/dist/video-js.css'
export default {
	name: 'living',
	data () {
		return {
			playerOptions: {
				playbackRates: [0.7, 1.0, 1.5, 2.0], //
          autoplay: true, //true,
          muted: false, // 
          loop: false, // 
          preload: 'auto', // <video>auto,
          language: 'zh-CN',
          aspectRatio: '16:9', //  - "16:9""4:3"
          fluid: true, // trueVideo.js player
          sources: [{
          src: 'http://aibeiwang.oss-cn-beijing.aliyuncs.com/yyh/test/50527a01eb7f2a047261cb88a95630b550527a01eb7f2a047261cb88a95630b5.mp4',  // 
          type: 'video/mp4'  // 
        }, {
          src: '//path/to/video.webm',
          type: 'video/webm'
        }],
          poster: "../../static/images/test.jpg", //
          // width: document.documentElement.clientWidth,
          notSupportedMessage: '', //Video.js
          controlBar: {
          timeDivider: true,
            durationDisplay: true,
            remainingTimeDisplay: false,
            fullscreenToggle: true  //
        }
			}
		}
    },
     computed: {
      ...mapState(['staticImgH', 'tokenH', 'playerStyleDetailedInfo'])
    },
	methods: {
        //
        toReturn(){
            this.$router.push('/')
        }
	}
}
</script>
 
<style scoped lang="stylus">
.container{
    width:100%;
    height:100%;
    >.play{
        width:100%;
        margin-top:0.6rem;
    }
}
.PlayerRanking_header{
        width:100%
        height:1.23rem;
        display :flex;
        align-items :center;
        justify-content :space-between;
        position :relative;
        padding:0 0.4rem;
        >img{
            width:0.32rem;
            height:0.56rem;
        }
        >span{
            font-size:0.48rem;
            color:rgba(0, 0, 0, 1);
        }
    }
</style>


